import React, { useEffect, useState, useContext } from 'react';
import './CartItem.css'

import fire from '../Firebase';
import useBasket from '../hooks/useBasket'
import UserContext from '../hooks/userContext';

const CartItem = (props) => {
  const user = useContext(UserContext);
  const [cartProduct, setCartProduct] = useState({});
  const [loading, setLoading] = useState(false);
  const { cartitem } = props;

  useEffect(() => {
    const unsubscribe = fire.db.collection('books').doc(cartitem.item.toString()).onSnapshot(docs => {
      if (docs.exists) {
        setCartProduct(docs.data());
        setLoading(false);
      }
    });
    return () => {
      unsubscribe();
    }
  }, [cartitem])

  const { dispatch } = useBasket(user);
  return (
    <div className="cartitem">
      {loading ?
        <img alt="loading" src="https://media3.giphy.com/media/3oEjI6SIIHBdRxXI40/giphy.gif" /> :
        <img alt={cartProduct.price} src={cartProduct.img} />}
      <div className="cartitem__details">
        <p>Price : {cartProduct.price}  </p>
        <p>Quantity : {props.cartitem.count}</p>
        <p>Subtotal : {cartProduct.price && (parseFloat(cartitem.count) * parseFloat(cartProduct.price)).toFixed(2)}
        </p>
      </div>
      <button
        onClick={() => dispatch({
          type: "REMOVE_FROM_BASKET", payload: {
            price: cartProduct.price,
            id: props.cartitem.item.toString()
          }
        })}>Remove</button>
    </div>
  )
}

export default CartItem;