import React, { useEffect, useContext, useState } from 'react';
import Nav from './Nav';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import { List } from '@material-ui/core';
import CartItem from './CartItem'
import fire from '../Firebase'
import UserContext from '../hooks/userContext';
import ClipLoader from "react-spinners/ClipLoader";
import Alert from '@material-ui/lab/Alert';
import AlertTitle from '@material-ui/lab/AlertTitle';

import './Cart.css'
const Cart = (props) => {
  const css = {
    'display': 'flex',
    'margin': 'auto',
  }
  const user = useContext(UserContext);
  const [cart, setCart] = useState({});
  const [total, setTotal] = useState(0);
  const [loading, setLoading] = React.useState(true);
  const [error, setEror] = React.useState('');
  useEffect(() => {
    const userCartRef = fire.db.collection('cart').doc(user.email);
    const unsubscribe = userCartRef.onSnapshot((doc) => {
      if (doc.exists) {
        let gTotal = 0;
        setCart(doc.data());
        doc.data().products.length <= 0 && setEror("No Items in the Cart !");
        doc.data().products.forEach((item, index) => {
          gTotal = gTotal + (item.price * item.count);
        })

        setTotal(gTotal);
        setLoading(false);
      } else {
        setEror("No Items in the Cart !");
        setLoading(false);
      }
    });
    return () => {
      unsubscribe();
      setTotal(0);
    }
  }, [user.email]);
  return loading ?
    <ClipLoader color={"black"} css={css} loading={loading} size={150} /> :
    (
      <div className="cart">
        <Nav
          isCart={props.isCart}
          basket={props.basket}
          cartLength={props.cartLength}
          search={props.search}
          handleAuth={props.handleAuth}
          handleSearch={props.handleSearch}
        />
        <React.Fragment>
          <CssBaseline />
          <Container fixed>
            {
              error ?
                <Alert severity="info">
                  <AlertTitle>Add Item to Cart</AlertTitle>
                  {error}
                </Alert> :
                <List component="div">
                  {
                    cart.products !== undefined && cart.products.map(cartitem =>
                      <CartItem
                        key={cartitem.item}
                        cartitem={cartitem} />
                    )
                  }
                </List>}
            <div className="cart__placeOrder">
              <h1>Total :  {total && total.toFixed(2)}</h1>


              <button
                className={cart.products.length > 0 ? "cart__placeOrderEnabled" : "cart__placeOrderDisabled"}>
                Place The Order
              </button>

            </div>
          </Container>

        </React.Fragment>

      </div>
    )
}

export default Cart;