import React from 'react';
import { useLocation } from "react-router-dom";
import './BookDescription.css';

const BookDescription = (props) => {
	let { book } = useLocation();
	return (
		<div className="bookdescription">
			<div className="bookdescription__title">
				<img src={book.book.img} alt={book.book.id} />
				<h1>{book.book.name}</h1>
			</div>
			<h3>Author : {book.book.author}</h3>
			<p>
				{book.book.description}
			</p>
			<h3>Price : {book.book.price}</h3>
			<h3>Type : {book.book.type}</h3>
			<h3>Categary :{book.book.category}</h3>
		</div>
	)
}

export default BookDescription;